
.logo--footer{
    width: 100%;
    display: flex;
    justify-content: start;
    img{
        width: 50%;
        height: 100%;
    }
}
.footer--container{
    background-color: #212529;
    color: #F8F9FA;
    margin-bottom: 0;
    padding: 2rem;
}

.iconos--footer--container--item{
    display: flex;
    gap: 2rem;
    margin-bottom: 3rem;
    align-items: center;
    p{
        font-size: 2rem;
    }
}
.derechos-reservados{
    font-size: 1.3rem;
    text-align: center;
    margin-top: 3rem;
}
@media (min-width: 765px){

        .iconos--footer{
            display: flex;
            width: 100%;
            justify-content: center;
        }

        .logo--footer{         
            display: flex;
            justify-content: center;
            width: 100%;
            height: 5rem;
            img{
                width: 30rem;
            }
        }
        .iconos--footer--container--item{
            font-size: 1.5rem;
            p{
                
            font-size: 1.5rem;
            }
        }
}